import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import 'echarts-wordcloud'
import {LineChart, BarChart, LineSeriesOption, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'

import {downloadPng} from "../utils/download";

interface BarLineChartProps {
    width?: string;
    height?: string;
    title?: string;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([LineChart, BarChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer])

function WordCloudChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null)
    const {width = '100%', height = '400px', title = 'Title'} = props;
    const option = {
        title: {
            text: title,
            textStyle: {
                fontSize: 24
            }
        },
        shape: 'circle',
        left: 'center',
        top: 'center',
        width: '80%',
        height: '80%',

        grid: {
            backgroundColor: 'white',
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
        },
        tooltip: {},
        series: [
            {
                type: 'wordCloud',
                data: [
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                    {name: 'FTI', value: 800},
                    {name: 'Apple', value: 100},
                    {name: '', value: 80, textStyle: {color: 'red'}},
                    {name: '', value: 40, textStyle: {color: 'blue'}},
                    {name: '', value: 150},
                ]
            }
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>

            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default WordCloudChart
