import React from 'react'
import {Card, Tooltip} from "element-react";
import defaultImageUrl from '../assets/default.jpeg'

export interface ProductCardProps {
    order?: number;
    tagContext?: string;
    imageUrl?: string;
    name: string;
    price: number;
    code: string;
    resolution?: string;
    feature?: string;
    certification?: string;
}

function ProductCard(props: ProductCardProps) {
    const {
        order,
        tagContext,
        imageUrl = defaultImageUrl,
        name,
        price,
        code,
        resolution,
        feature,
        certification
    } = props
    return (
        <Card className="relative cursor-pointer" style={{overflow: 'visible'}}>
            {/*Order*/}
            {order && <p className="absolute top-1 text-primary text-2xl">#{order}</p>}

            {/*Tag*/}
            {
                tagContext &&
                <div className="absolute top-0 right-0 px-2 py-1 bg-primary text-sm text-white">
                  <p>{tagContext}</p>
                </div>
            }

            <img className="w-1/1 my-4" src={imageUrl} alt={`${name} cover`} width={200} height={200}/>
            <div className="flex justify-between items-center">
                <Tooltip content={name} effect="light" className="overflow-y-hidden">
                    <h2 className="text-lg text-context text-ellipsis">{name}</h2>
                </Tooltip>
                <p className="text-lg text-context">${price}</p>
            </div>
            <p className="text-lg text-context">{code}</p>

            {/*Hover: Information*/}
            {/*<div className="absolute right-0 bottom-0 left-0 z-20 transform translate-y-full bg-white">*/}
            <div className="mt-4">
                <p>Resolution: <span className="text-sm text-context">{resolution}</span></p>
                <p>Function: <span className="text-sm text-context">{feature}</span></p>
                <p>Certification: <span className="text-sm text-context">{certification}</span></p>
            </div>
        </Card>
    )
}

export default ProductCard


