import React, {useEffect, useState} from 'react'
import {Card, Select} from "element-react";

import BarLineChart from "../components/BarLineChart";
import DoughnutChart from "../components/DoughnutChart";
import PercentageDoughnutChart from "../components/PercentageDoughnutChart";
import HorizontalBarChart from "../components/HorizontalBarChart";
import InfoCard from '../components/InfoCard'
import VerticalProgressBarChart from "../components/VerticalProgressBarChart";
import {init30DaysRangeDateType, initCategoryOptions, initRegionOptions} from "../utils/mock";
import {
    getNewCommentBarChart,
    getPriceDistributionBarLineChart,
    getTopBrandPieChart,
    getTopProductBarChart
} from "../apis/market";
import {
    NewCommentBarChart, NewCommentBarItem,
    PriceDistributionBarLineChart,
    ProductBarChart,
    SearchParams,
    TopBrandPieItem
} from "../types/market";
import {formatDate} from "../utils/common";
import {getAmazonsChoice, getSponsored} from "../apis/seo";
import {AmazonsChoice, Sponsored} from "../types/seo";
import {PieChartItem} from "../types/chart";
import ProductCards from "../components/ProductCards";
import {ProductsParams, ProductsResponse} from "../types/product";
import {getProducts} from "../apis/product";

function formatData(data: NewCommentBarChart): [string[], number[]] {
    const formatContent = data.items.content.sort((a: NewCommentBarItem, b: NewCommentBarItem) => b.gap - a.gap)
    const yAxisData = formatContent.map((it: NewCommentBarItem) => it.asin)
    const seriesData = formatContent.map((it: NewCommentBarItem) => it.gap)
    return [yAxisData, seriesData]
}

function AmazonTop100DashBoard() {
    const [category, setCategory] = useState('webcam')
    const [region, setRegion] = useState('US')
    const [date, setDate] = useState(init30DaysRangeDateType)

    const [productsPage, setProductsPage] = useState(1)

    const [distribution, setDistribution] = useState<PriceDistributionBarLineChart | null>(null)
    const [amazonsChoice, setAmazonsChoice] = useState<AmazonsChoice | null>(null)
    const [sponsored, setSponsored] = useState<Sponsored | null>(null)
    const [topBrandData, setTopBrandData] = useState<PieChartItem[]>([])
    const [brandProducts, setBrandProducts] = useState<ProductBarChart | null>(null)
    const [newComment, setNewComment] = useState<NewCommentBarChart | null>(null)
    const [products, setProducts] = useState<ProductsResponse | null>(null)

    const categoryOptions = initCategoryOptions()
    const regionOptions = initRegionOptions()

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date)
            const params: ProductsParams = {category, region, page: productsPage, start_at, end_at}
            const {data} = await getProducts(params)
            setProducts(data)
        }
        fetchData()
    }, [category, region, date, productsPage])

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date)
            const params: SearchParams = {
                category,
                region,
                start_at,
                end_at,
            }
            setDistribution((await getPriceDistributionBarLineChart(params)).data)
            setAmazonsChoice((await getAmazonsChoice(params)).data)
            setSponsored((await getSponsored(params)).data)
            const {data: pie} = await getTopBrandPieChart(params)
            const pieChartItems = pie.items.map((it: TopBrandPieItem): PieChartItem => ({name: it.brand, value: it.count}))
            setTopBrandData(pieChartItems)
            setBrandProducts((await getTopProductBarChart(params)).data)
            setNewComment((await getNewCommentBarChart(params)).data)
        }
        fetchData()
    }, [category, region, date]);

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-end mb-4">
                <Select className="mr-4" value={category} placeholder="Select" onChange={value => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select" onChange={value => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>

                <p className="text-xl text-context">Last Update: 2021/09/19</p>
            </div>

            {/* Dash Board */}
            <div className="flex">
                <div className="w-7/12 pr-2">
                    <Card className="mb-4">
                        <BarLineChart title="Price Distribution" height="400px" data={distribution}/>
                    </Card>
                    <div className="flex mb-4">
                        <div className="w-1/2 pr-2">
                            <InfoCard title="Most Popular Resolution" context="1080P" reviewsContext="2,000"/>
                        </div>
                        <div className="w-1/2 pl-2">
                            <InfoCard title="Most Popular Price Range" context="$50~60" reviewsContext="1,200"/>
                        </div>
                    </div>
                    <Card>
                        {
                            newComment && <HorizontalBarChart title="Top Product" height="400px" data={formatData(newComment)}/>
                        }
                    </Card>
                </div>
                <div className="w-5/12 pl-2">
                    <Card className="mb-4">
                        <DoughnutChart title="Top Brand" height="300px" data={topBrandData}/>
                    </Card>
                    <div className="flex mb-4">
                        <div className="w-1/2 pr-2">
                            <Card>
                                <PercentageDoughnutChart
                                    height="200px"
                                    title="Sponsored Rate"
                                    percentage={sponsored ? Math.round(sponsored.sponsored_rate * 100) : 0}
                                />
                            </Card>
                        </div>
                        <div className="w-1/2 pl-2">
                            <Card>
                                <PercentageDoughnutChart
                                    height="200px"
                                    title="Amazon's Choice"
                                    percentage={amazonsChoice ? Math.round(amazonsChoice.amazons_choice_rate * 100) : 0}
                                />
                            </Card>
                        </div>
                    </div>
                    <Card>
                        <VerticalProgressBarChart title="Top Product" height="400px" data={brandProducts}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex justify-between items-end">
                    <p className="text-2xl">Recent Top 100</p>
                    <Select value={region} placeholder="Select" onChange={(value: string) => setRegion(value)}>
                        {
                            regionOptions.map(it => {
                                return <Select.Option key={it.value} label={it.label} value={it.value}/>
                            })
                        }
                    </Select>
                </div>

                {
                    products &&
                    <ProductCards
                      data={products.prods}
                      region={region}
                      page={products.info.current_page}
                      totalPage={products.info.max_page}
                      onChange={setProductsPage}
                    />
                }
            </div>
        </div>
    )
}

export default AmazonTop100DashBoard
