import React, {useMemo} from 'react'

interface Item {
    source: string;
    count: number;
}

interface ProgressChartProps {
    title?: string;
    data: Item[]
}

function ProgressChart(props: ProgressChartProps) {
    const {title = '', data} = props;
    const total = useMemo(() => data.reduce((prev, curr) => prev + curr.count, 0), [data]);

    const colors = ["#FB8C3B", "#ACACAC"];
    const getNameColorStyles = (index: number) => ({
        backgroundColor: colors[index]
    });
    const getBarStyles = (count: number, index: number) => {
        return {
            background: colors[index],
            width: total ? `calc(100% / ${total} * ${count})` : '50%',
        };
    };

    return (
        <div>
            {title && <h1 className="text-2xl">{title}</h1>}
            <div className="pt-4">
                <div className="mx-auto">
                    <div className="flex justify-start" style={{height: '100px'}}>
                        {
                            data.map((item: Item, index: number) => (
                                <div className="flex justify-center items-center"
                                     key={index}
                                     style={getBarStyles(item.count, index)}
                                >
                                    <p className="text-2xl text-white">{item.count ? Math.round(item.count / total * 100) : 0}%</p>
                                </div>
                            ))
                        }
                    </div>
                </div>
                <div className="flex justify-center items-center mt-4">
                    {
                        data.map((item: Item, index: number) => (
                            <div className="flex mr-8" key={index}>
                                <div className="w-4 h-4 mr-2" style={getNameColorStyles(index)}/>
                                <h2>Form {item.source}</h2>
                            </div>
                        ))
                    }
                </div>
            </div>
            {/*<p className="flex justify-center items-center text-3xl text-context">No Data</p>*/}
        </div>
    )
}

export default ProgressChart
