import React from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'

import Header from './components/Header'
import Home from './pages/Home'
import Login from './pages/Login'
import AmazonTop100 from './pages/AmazonTop100'
import CustomsData from "./pages/CustomsData";

function App() {
    return (
        <Router>
            <Header/>
            <main className="h-main overflow-y-scroll">
                <Switch>
                    <Route path="/login">
                        <Login/>
                    </Route>
                    <Route path="/amazon-top-100">
                        <AmazonTop100/>
                    </Route>
                    <Route path="/customs-data">
                        <CustomsData/>
                    </Route>
                    <Route path="/">
                        <Home/>
                    </Route>
                </Switch>
            </main>
        </Router>
    );
}

export default App;
