import React, {Dispatch, SetStateAction, useState} from 'react'
import ProductCard, {ProductCardProps} from "../components/ProductCard";
import {Pagination} from "element-react";
import ProductDialog from "./ProductDialog/index";
import {ProductItemResponse} from "../types/product";

export interface ProductCardsProps {
    region: string;
    totalPage: number;
    page: number;
    data: ProductItemResponse[];
    onChange: Dispatch<SetStateAction<number>>;
}

function ProductCards(props: ProductCardsProps) {
    const {data = [], page = 1, totalPage = 1, region = '', onChange} = props;
    const [asin, setAsin] = useState('')
    const [dialogVisible, setDialogVisible] = useState(false)

    const openDialogHandler = (asin: string) => {
        setAsin(asin)
        setDialogVisible(true)
    };

    return (
        <div>
            <div className="flex flex-wrap -mx-1 mt-4">
                {
                    data.map((it: ProductItemResponse) => {
                        const props: ProductCardProps = {
                            name: it.name,
                            price: it.price,
                            code: it.ASIN,
                            imageUrl: it.image_url[0],
                            tagContext: it.ranking === 1 ? 'Best Seller' : ''
                        }
                        return (
                            <div className="mx-1 mb-2" style={{width: 'calc(20% - 0.5rem)'}} onClick={() => openDialogHandler(it.ASIN)}>
                                <ProductCard {...props}/>
                            </div>
                        )
                    })
                }
            </div>

            <div className="flex justify-center my-8">
                <Pagination
                    layout="prev, pager, next"
                    pageCount={totalPage}
                    currentPage={page}
                    onCurrentChange={(value: number) => onChange(value)}/>
            </div>

            <ProductDialog visible={dialogVisible} region={region} asin={asin} setVisible={setDialogVisible}/>
        </div>
    )
}

export default ProductCards
