import React from 'react';
import {useHistory} from "react-router";

import LoginCard from './LoginCard'

import {setToken} from "../../utils/storage";
import {useAppDispatch} from "../../store/hooks";
import {setStoreToken} from '../../slices/authSlice';

function Login() {
    const dispatch = useAppDispatch();
    const history = useHistory()

    const handleLogin = (account: string, password: string) => {
        if (account && password) {
            setToken('my-token');
            dispatch(setStoreToken('my-token'));
            history.push('/amazon-top-100');
        }
    }

    return (
        <div className="flex justify-center items-center h-full bg-background">
            <LoginCard onLogin={handleLogin}/>
        </div>
    );
}

export default Login;
