import React, {useEffect, useState} from 'react'
import dayjs from 'dayjs'
import {Card, DateRangePicker, Pagination, Select, Tooltip} from "element-react";
import {AiFillInfoCircle} from 'react-icons/ai'
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import BarLineChart from "../components/BarLineChart";
import Collapse from "../components/Collapse";
import {init30DaysRangeDateType, initCategoryOptions, initRegionOptions} from "../utils/mock";
import StockChart from "../components/StockChart";
import {getInstockStockChart, getTable} from "../apis/market";
import {SearchParams, StockChartData, TableResponse} from "../types/market";
import {formatDate} from "../utils/common";
import {CHART_TYPE} from "../types/common";
import {ProductItemResponse, ProductsParams, ProductsResponse} from "../types/product";
import {getProducts} from "../apis/product";
import ProductCards from "../components/ProductCards";
import TableChart, {TableColumn} from "../components/TableChart";

function Inventory() {
    const [chartType, setChartType] = useState(CHART_TYPE.CHART)
    const [category, setCategory] = useState('webcam')
    const [region, setRegion] = useState('US')
    const [date, setDate] = useState<ElementReactLibs.dateType[]>(init30DaysRangeDateType())
    const [filter, setFilter] = useState<any>({});

    const [page, setPage] = useState(1)
    const [productsPage, setProductsPage] = useState(1)

    const [stock, setStock] = useState<StockChartData | null>(null)
    const [table, setTable] = useState<TableResponse | null>(null)
    const [products, setProducts] = useState<ProductsResponse | null>(null)

    const categoryOptions = initCategoryOptions()
    const regionOptions = initRegionOptions()

    const tableColumn: TableColumn[] = [
        {label: 'Product', prop: 'Product'},
        {label: 'Brand', prop: 'Brand'},
        {label: 'Stock Level', prop: 'Stock Level'},
        {label: 'Date', prop: 'Date'},
    ]

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date)
            const params: ProductsParams = {category, region, page: productsPage, start_at, end_at, filter}
            const {data} = await getProducts(params)
            setProducts(data)
            setProductsPage(data.info.current_page)
        }
        fetchData()
    }, [region, date, filter, productsPage])

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date)
            const params: SearchParams = {
                category,
                region,
                start_at,
                end_at,
                page,
                ...filter
            }
            const {data} = await getInstockStockChart(params)
            const {data: table} = await getTable(params);
            setTable(table)
            setStock(data)
        }
        fetchData()
    }, [region, date, filter, page])

    useEffect(() => {
        setFilter({})
    }, [category])

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE)
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={category} placeholder="Select" onChange={(value: string) => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select" onChange={(value: string) => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0}
                    onChange={setDate}
                />
                <Tooltip
                    className="mr-4"
                    effect="dark"
                    content={
                        <div style={{maxWidth: '200px'}}>
                            Sold Amount is estimated by multiplaying new reviews in each price range by 100.
                        </div>
                    }
                    placement="bottom"
                >
                    <AiFillInfoCircle className="text-2xl"/>
                </Tooltip>
                <div className="text-2xl cursor-pointer" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
            </div>

            <div className="flex">
                <div className="w-2/3 pr-2">
                    <Card className="mb-4" style={{minHeight: '642px'}}>
                        {
                            chartType === CHART_TYPE.CHART && <StockChart title="Stock Level" height="600px" data={stock}/>
                        }
                        {
                            chartType === CHART_TYPE.TABLE && table &&
                            <>
                              <TableChart height="572px" columns={tableColumn} data={table.items.content}/>
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={table.info.max_page}
                                  small={true}
                                  currentPage={table.items.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </>
                        }
                    </Card>
                </div>

                <div className="w-1/3 pl-2">
                    <Card>
                        <Collapse height="600px" type={category} onFilter={setFilter}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex justify-between items-end">
                    <p className="text-2xl">Date... Out of Stock</p>
                    <p className="text-2xl">Sort by out of stock period</p>
                </div>
                {
                    products &&
                    <ProductCards
                      data={products.prods}
                      region={region}
                      page={products.info.current_page}
                      totalPage={products.info.max_page}
                      onChange={setProductsPage}
                    />
                }
            </div>
        </div>
    )
}

export default Inventory
