import request from './https'
import {AxiosPromise} from 'axios'
import {AmazonsChoice, AmazonsChoiceRate, Sponsored, SponsoredAvg, SponsoredRate} from "../types/seo";
import {SearchParams} from "../types/market";

export function getSponsored(data: SearchParams) {
    return request({
        url:'/SEO/sponsored',
        method: 'GET',
        params: data
    }) as AxiosPromise<Sponsored>
}

export function getSponsoredAvg(data: SearchParams) {
    return request({
        url:'/SEO/sponsored_avg_rank_increase',
        method: 'GET',
        params: data
    }) as AxiosPromise<SponsoredAvg>
}

export function getSponsoredRate(data: SearchParams) {
    return request({
        url:'/SEO/sponsored_rate',
        method: 'GET',
        params: data
    }) as AxiosPromise<SponsoredRate>
}

export function getAmazonsChoice(data: SearchParams) {
    return request({
        url:'/SEO/amazons_choice',
        method: 'GET',
        params: data
    }) as AxiosPromise<AmazonsChoice>
}

export function getAmazonsChoiceRate(data: SearchParams) {
    return request({
        url:'/SEO/amazons_choice_rate',
        method: 'GET',
        params: data
    }) as AxiosPromise<AmazonsChoiceRate>
}

