import React from 'react'
import {Table} from 'element-react'
import {HiDownload} from 'react-icons/hi'
import {
    TableItemResponse,
} from "../types/market";

export interface TableColumn{
    label: string;
    prop: string;
    sortable?: boolean;
    width?: number;
}

interface TableChartProps {
    width?: string;
    height?: string;
    columns: TableColumn[]
    data: TableItemResponse[]
}

function TableChart(props: TableChartProps) {
    const {width = '100%', height = '400px', columns = [], data = []} = props;

    const onDownload = async () => {
        console.log('download');
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>

            <div style={{width, height}}>
                <Table
                    className="h-full pt-4"
                    style={{width}}
                    columns={columns}
                    data={data}
                    border={true}
                />
            </div>
        </div>
    )
}

export default TableChart
