import React, {useState} from 'react';
import logo from 'assets/logo.png';
import {NavLink, useHistory} from "react-router-dom";
import {RiArrowDownSLine, RiSubtractLine} from 'react-icons/ri'
import {useAppDispatch, useAppSelector} from "../store/hooks";
import {clearStoreToken, selectAuthToken} from "../slices/authSlice";
import {clearToken} from "../utils/storage";

function Header() {
    const dispatch = useAppDispatch();
    const history = useHistory();
    const token = useAppSelector(selectAuthToken);
    const [isActive, setIsActive] = useState(false);

    const userToggleHandler = () => {
        setIsActive(value => !value)
    }

    const settingHandler = () => {
        history.push('/setting')
    }

    const logOutHandler = () => {
        dispatch(clearStoreToken());
        clearToken();
        history.push('/login')
    }

    return (
        <header className="h-16 py-4 px-16 flex items-center bg-menu">
            <NavLink to="/">
                <img src={logo} className="w-auto h-12 mr-24" alt="logo"/>
            </NavLink>
            <div className="flex items-center">
                <NavLink className="text-white text-xl mr-12 hover:text-primary"
                         to="/amazon-top-100"
                >Amazon Top 100</NavLink>
                <NavLink className="text-white text-xl mr-12 hover:text-primary"
                         to="/customs-data"
                >Customs Data</NavLink>
            </div>
            {
                token
                    ?
                    <div
                        className="relative text-white text-xl text-center ml-auto h-16 flex justify-center items-center px-4"
                    >
                        <a href="#" className="flex items-center" onClick={userToggleHandler}>
                            <p className="mr-2">User Name</p>
                            <RiArrowDownSLine className={`${isActive && '-rotate-180'} transform transition`}/>
                        </a>

                        <div
                            className={`${isActive && 'h-24'} h-0 overflow-hidden absolute -bottom-24 left-0 z-50 bg-menu w-full transition`}>
                            <a href="#" onClick={settingHandler}>
                                <p className="border-b-2 border-solid border-white p-2">Setting</p>
                            </a>
                            <a href="#" onClick={logOutHandler}>
                                <p className="p-2">Log out</p>
                            </a>
                        </div>
                    </div>

                    :
                    <NavLink className="text-white text-xl ml-auto" to="/login">
                        Sign In
                    </NavLink>
            }
        </header>
    );
}

export default Header;
