import React, {useState} from "react";
import {Button, Card, Input} from "element-react";

type LoginCardProp = {
    onLogin: Function
}

function LoginCard({onLogin}: LoginCardProp) {
    const [account, setAccount] = useState('');
    const [password, setPassword] = useState('');

    return (
        <Card className="h-card" style={{width: '500px'}}>
            <h1 className="mb-4 text-4xl font-medium">Login</h1>
            <div>
                <div className="relative py-4">
                    <p className="mb-4 text-2xl font-medium">Account</p>
                    {/*@ts-ignore*/}
                    <Input value={account} onChange={setAccount}/>
                </div>
                <div className="relative py-4">
                    <p className="mb-4 text-2xl font-medium">Password</p>
                    {/*@ts-ignore*/}
                    <Input type="password" value={password} onChange={setPassword}/>
                </div>
            </div>

            <div className="py-4 flex justify-end items-center">
                <Button type="primary" onClick={() => onLogin(account, password)}>Login</Button>
            </div>
        </Card>
    );
}

export default LoginCard;
