import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {LineChart, LineSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'

import {downloadPng} from "../utils/download";
import {SponsoredAvg} from "../types/seo";

interface BarLineChartProps {
    width?: string;
    height?: string;
    title?: string;
    data: SponsoredAvg
}

type ECOption = echarts.ComposeOption<LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([LineChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer])

function AverageChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', data} = props;
    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: 24
            }
        },
        grid: {
            backgroundColor: 'white',
            top: 70,
            right: 50,
            left: 50
        },
        legend: {
            data: ['Start at 75+', 'Start at 75 - 50', 'Start at 50 - 25'],
            bottom: 0
        },
        tooltip: {
            trigger: 'axis'
        },
        xAxis: [
            {
                type: 'category',
                data: ['Week1', 'Week2', 'Week3', 'Week4']
            }
        ],
        yAxis: [
            {
                type: 'value',
                name: 'Rank',
            },
        ],
        series: [
            {
                type: 'line',
                name: 'Start at 75+',
                data: [1, 2, 3, 4]
            },
            {
                type: 'line',
                name: 'Start at 75 - 50',
                data: [4, 3, 2, 1]
            },
            {
                type: 'line',
                name: 'Start at 50 - 25',
                data: [2, 4, 1, 3]
            },
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>

            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default AverageChart
