import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {BarChart, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";
import { TopBrandBarChart, TopBrandPieItem} from "../types/market";

interface VerticalProgressBarChartProps {
    width?: string;
    height?: string;
    title?: string;
    data: TopBrandBarChart | null
}

type ECOption = echarts.ComposeOption<BarSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption>;

echarts.use([BarChart, TitleComponent, GridComponent, TooltipComponent, CanvasRenderer])

function formatData(data: TopBrandBarChart | null): [string[], number[]] {
    if (!data) return [[], []]
    const formatContent = data.items.content.sort((a: TopBrandPieItem, b: TopBrandPieItem) => b.count - a.count)
    const yAxisData = formatContent.map((it: TopBrandPieItem) => it.brand)
    const seriesData = formatContent.map((it: TopBrandPieItem) => it.count)
    return [yAxisData, seriesData]
}

function VerticalProgressBarChart(props: VerticalProgressBarChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null)
    const {width = '100%', height = '400px', title = 'Title', data = null} = props;

    const [yAxisData, seriesData] = formatData(data);

    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: 24
            }
        },
        grid: {
            top: 70,
            right: 20,
            bottom: 60,
            left: 50,
        },
        tooltip: {
            trigger: 'axis'
        },
        xAxis: [
            {
                type: 'category',
                data: yAxisData,
                axisLabel: {
                    interval: 0
                    // rotate: 40
                }
            }
        ],
        yAxis: [
            {
                type: 'value',
                splitLine: {
                    show: false
                }
            }
        ],
        series: [
            {
                type: 'bar',
                name: 'Brand',
                yAxisIndex: 0,
                showBackground: true,
                data: seriesData,
                barWidth: 20,
                itemStyle: {
                    color: 'yellow',
                }
            },
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>

            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default VerticalProgressBarChart
