import React, {useState} from 'react'
import dayjs from 'dayjs'
import {Card, DateRangePicker, Select, Table} from "element-react";
import {HiDownload} from "react-icons/hi";

import BarLineChart from '../components/BarLineChart';
import DoughnutChart from "../components/DoughnutChart";
import {init30DaysRangeDateType, initCategoryOptions, initRegionOptions} from "../utils/mock";

function CustomsDataDashBoard() {
    const [brand, setBrand] = useState('guangzhou_shirui');
    const [code, setCode] = useState('all');
    const [date, setDate] = useState<ElementReactLibs.dateType[] | undefined>(init30DaysRangeDateType());

    const brandOptions = initCategoryOptions();
    const codeOptions = initRegionOptions();
    const tableColumns: ElementReact.TableColumn[] = [
        {type: 'index'},
        {label: 'Exporter', prop: 'exporter'},
        {label: 'HS Code', prop: 'hs_code'},
        {label: 'Importer', prop: 'importer'},
        {label: 'Destination Country', prop: 'destination_country', width: 180},
        {label: 'Value', prop: 'value'},
        {label: 'Quantity', prop: 'quantity'},
        {label: 'Quantity Unit', prop: 'quantity_unit'},
        {label: 'Unit Price', prop: 'unit_price'},
    ]

    const onDownload = () => {
        console.log('table download');
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-end mb-4">
                <Select className="mr-4" value={brand}>
                    {
                        brandOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={code}>
                    {
                        codeOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0}
                    onChange={setDate}
                />
            </div>

            {/* Dashboard Charts */}
            <Card className="mb-12">
                <div className="flex">
                    <div className="w-2/3">
                        <BarLineChart title="Weight, Import Value and Bill of Lading" height="600px" data={null}/>
                    </div>
                    <div className="w-1/3">
                        <DoughnutChart title="Top Importer by Import Value" height="300px" data={[]}/>
                        <DoughnutChart title="Top Importer by Bill of Lading" height="300px" data={[]}/>
                    </div>
                </div>
            </Card>

            {/* Dashboard Table */}
            <Card className="relative mb-12 pt-8 pb-4">
                <HiDownload className="absolute right-4 top-4 text-3xl cursor-pointer z-30" onClick={onDownload}/>

                <Table
                    style={{width: '100%'}}
                    columns={tableColumns}
                    data={tableData}
                />
            </Card>
        </div>
    )
}

